.conversation-messages{
    overflow-y: scroll;
    height: 67vh;
    padding: 0vh,1vh,0vh,1vh;
    padding-bottom: 1vh;
}

.input-container{
    display: flex;
    align-items: center;
    justify-content:space-between;
    overflow: auto;
}

.input-container button{
    height:37px;
    margin-top: 10vh;
    width: 5vw;
    font-size: 16px;
}
.input-container button:hover{
    background-color: silver;
}

.input-message input{
    margin-top: 8vh;
    width: 45.5vw;
    height: 30px;
    font-size: 16px;
   margin-left: 0.5vh;
   float: left;
}

.message-container{
    display: flex;    
}

.message-content{
    font-size: 18px;
    background-color: rgb(16, 123, 230);
    margin: 0.5vh,0vh,0.5vh,0vh;
    margin-top: 1vh;
    border-bottom: 1px solid white;
    padding: 0.5vh;
    border-radius: 10px;
    min-width: 8vh;
    color: white;
}

.chatscreen{
    width: 100%;
    background-color: rgb(16, 123, 230);
    color: white;
}

.chat{
    width: 65%;
    height: 90vh;
    margin-top: auto;
    float: left;
    background-color:rgb(66, 65, 65);
    overflow: auto;
}

.chat p{
    text-align: center;
    font-size: 18px;
}

.encryption-elements{
    background-color: rgb(16, 123, 230);
    color: white;
    height: 90vh;
    font-size: 18px;
    overflow: auto;
}

input{
    width:20vw;
    height: 20px;
    font-size: 16px;
}

.nochat{
    display: flex;
    align-items: center;
    justify-content: center;
    height:87vh;
    background-color:rgb(66, 65, 65);

}

.nochat p{
    font-size: 28px;
}





